.DiaryEditor {
  border: 1px solid black;
  text-align: center;
  margin: 20px;
}

.diary__author,
.diary__content {
  width: 300px;
  padding: 10px;
}

.diary__content {
  height: 70px;
}

.diary_point {
  width: 160px;
  padding: 5px;
}

.diary__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.diary__author,
.diary__content,
.diary__emotion,
.diary.submit-btn {
  margin: 10px 0;
}

.diary__list {
  width: 300px;
  height: 80px;
  border: 1px solid black;
  margin: 10px;
}

.diary.submit-btn {
  padding: 12px;
  background-color: pink;
  color: white;
  border: none;
  border-radius: 5px;
}

.diary__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
  width: 300px;
  padding: 10px;
}
